import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {
  dates: Array<Date> = 
  [
    new Date (2022,8,16),
    new Date (2022,8,17),
    new Date (2022,8,23),
    new Date (2022,8,30),
    new Date (2022,9,1),
    new Date (2022,9,7),
    new Date (2022,9,8),
    new Date (2022,9,14),
    new Date (2022,9,21),
    new Date (2022,9,22),
    new Date (2022,10,11),
    new Date (2022,10,12),
    new Date (2022,10,18),
    new Date (2022,10,25),
    new Date (2022,11,2),
    new Date (2022,11,16),
    new Date (2023,0,13),
    new Date (2023,0,14),
    new Date (2023,0,27),
    new Date (2023,0,28)
  ];

  public daysLeft: number;
  public remainingDays: Array<Date>;
  public currentDays: Array<Date>;
  public passedDays: Array<Date>;

  public cols = 5;

  ngOnInit(): void {
    if(window.innerHeight > window.innerWidth)
    {
      this.cols = 3;
    }

    
    setInterval(() => {
      let currentDate: Date = new Date();
      currentDate.setUTCHours(0,0,0,0);
      
      this.remainingDays = new Array<Date>();;
      this.passedDays = new Array<Date>();
      this.currentDays = new Array<Date>();
      
      let currentDateNumber = this.getDateNumber(currentDate);
      for(let i = 0;i<this.dates.length;i++)
      {
        let date = this.dates[i];
        let dateNumber = this.getDateNumber(date);

        if(dateNumber > currentDateNumber)
        {
          this.remainingDays.push(date);
        }
        else if(dateNumber === currentDateNumber)
        {
          this.currentDays.push(date);
        }
        else if(dateNumber < currentDateNumber)
        {
          this.passedDays.push(date);
        }

      }

      this.daysLeft = this.remainingDays.length;
    },1000)

  }

  private getDateNumber(date:Date)
  {
    let currentYear = date.getFullYear();
    let currentMonth = date.getMonth();
    let currentDay = date.getDate();

    let dateString: string = currentYear+"";
    if(currentMonth < 10)
    {
      dateString = `${dateString}0${currentMonth}`;
    }
    else
    {
      dateString = dateString + currentMonth;
    }

    if(currentDay < 10)
    {
      dateString = `${dateString}0${currentDay}`;
    }
    else
    {
      dateString = dateString + currentDay;
    }

    let dateNumber:number = Number(dateString);

    return dateNumber;
  }

}
